@import url(https://fonts.googleapis.com/css2?family=Karla:wght@200;300;400;500;600;700&family=Kumbh+Sans:wght@400;500;600;700&display=swap);
@font-face {
    font-family: 'Karla', sans-serif;
    src: url('https://fonts.googleapis.com/css2?family=Karla:wght@200;300;400;500;600;700&family=Kumbh+Sans:wght@400;500;600;700&display=swap');
}

body{
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    font-family: 'Karla', sans-serif;
    background-color: #f8f8f8;
}
.home{
    margin: 2rem 0;
}

.home .boxes{
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.home h1{
    font-size: 50px;
    margin: 70px 0 100px 0;
    text-align: center;
    font-weight: 100;
    letter-spacing: 3px;
}

.boxes .box1{
    background: url(/Dawn-merch/static/media/pexels-cottonbro-9906547.da455c4f.jpg) center no-repeat;
    height: 32rem;
    width: 25rem;
    background-size: 100%;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.boxes .box2{
    background: url(/Dawn-merch/static/media/pexels-artem-beliaikin-1036856.881228d5.jpg) center no-repeat;
    height: 35rem;
    width: 25rem;
    background-size: 210%;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.boxes .box3{
    background: url(/Dawn-merch/static/media/pexels-jaysen-scott-1858488.380da082.jpg) center no-repeat;
    height: 32rem;
    width: 25rem;
    background-size: 110%;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.home h2{
    font-size: 40px;
    margin: 70px;
    text-align: center;
    font-weight: 300;
    letter-spacing: 1px;
}

.home button{
    font-size: 15px;
    margin-bottom: 30px;
    padding: 10px;
    background: #f2f2f2;
    border: none;
    border-radius: 5px;
}

.home button:hover{
    cursor: pointer;
}

.loading{
    margin: 7rem 0;
    display: flex;
    justify-content: center;
    font-size: 20px;
}

/* Responsiveness */
@media screen and (max-width: 1300px) {
    .boxes .box1{
        height: 25rem;
        width: 20rem;
    }

    .boxes .box2{
        height: 28rem;
        width: 20rem;
    }

    .boxes .box3{
        height: 25rem;
        width: 20rem;
    }
}

@media screen and (max-width: 1080px) {
    .home .boxes{
        flex-direction: column;
    }

    .boxes .box1{
        height: 25rem;
        width: 25rem;
    }

    .boxes .box2{
        height: 25rem;
        width: 25rem;
        margin-top: 50px;
    }

    .boxes .box3{
        height: 25rem;
        width: 25rem;
        margin-top: 50px;
    }
}

@media screen and (max-width: 600px) {
    .home h1{
        font-size: 40px;
        margin: 50px 10px 70px 10x;
    }
}

@media screen and (max-width: 450px) {
    .home h1{
        margin: 40px 10px 60px 10px;
    }

    .boxes .box1{
        height: 22rem;
        width: 80%;
    }

    .boxes .box2{
        height: 22rem;
        width: 80%;
        margin-top: 50px;
    }

    .boxes .box3{
        height: 22rem;
        width: 80%;
        margin-top: 50px;
    }

    .home h2 {
        font-size: 35px;
        margin: 60px 10px;
    }
}

@media screen and (max-width: 360px) {
    .home h1{
        font-size: 35px;
    }

    .home h2 {
        font-size: 30px;
    }
}
.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 5rem;
}

.navbar .logo p{
    font-size: 25px;
    font-weight: bold;
}

.nav-links{
    display: flex;
    font-size: 17px;
}

.navbar p a{
    text-decoration: none;
    color: black;
}

.navbar li{
    list-style: none;
    margin: 0 20px;
    color: #636363;
}

.navbar li a{
    text-decoration: none;
    color: #636363;
}

.navbar .toggle{
    color: #d32020;
}

.navbar .buttons{
    display: flex;
}

.navbar .btn{
    text-align: center;
    border: 2px solid #636363;
    border-radius: 5px;
    margin: 0 5px;
    width: 100px;
}

.navbar button{
    border: none;
    padding: 8px 8px;
    background: transparent;
    font-size: 15px;
}

.navbar button a{
    text-decoration: none;
    color: black;
}

.btn:hover, .btn:hover>button a{
    cursor: pointer;
    background: black;
    color: white;
}

/* Responsiveness */
@media screen and (max-width: 600px) {
    .navbar{
        padding: 1rem 3rem;
    }
}

@media screen and (max-width: 360px) {
    .navbar {
        padding: 1rem 2rem;
    }
}

@media screen and (max-width: 350px) {
    .navbar{
        padding: 1rem 2rem;
    }

    .navbar .logo p {
        font-size: 20px;
    }

    #mobile-navbar .icon {
        font-size: 20px !important;
        top: 35px !important;
    }
}
#mobile-navbar{
    display: none;
}

#mobile-navbar .icon{
    font-size: 25px;
    position: absolute;
    top: 2.5rem;
    cursor: pointer;
    z-index: 2;
}

.mobile-nav-links{
    position: fixed;
    z-index: 1;
    width: 100%;
    height: 100%;
    background: black;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    transition: 0.3s ease-in-out;
    padding: 0;
}

.mobile-nav-links .links li{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    transition: 0.2s ease-in-out;
    padding: 20px 0;
}

@media screen and (max-width: 950px) {
    #desktop-navbar{
        display: none;
    }

    #mobile-navbar{
        display: block;
    }

    .navbar li a{
        color: white;
    }
}

@media screen and (max-width: 400px) {
    #mobile-navbar .icon{
        right: 30px;
    }
}

/* Utility Classes */
#hide{
    display: none;
}

.white{
    color: white;
}

.hide-overflow{
    overflow: hidden;
}
.product{
    margin: 4rem 0;
}

.product h2{
    font-size: 45px;
    text-align: center;
}

.product hr{
    width: 90%;
}

.products .button{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.button .btn{
    font-size: 15px;
    margin: 10px;
    padding: 10px;
    background: white;
    border: 1px solid black;
    border-radius: 5px;
    transition: background-color 0.2s ease-in-out;
}

.button .btn:hover{
    background-color: black;
    color: white;
}

/*Card*/
.products .cards{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.products .card{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
    border: 1px solid #dfdfdf;
    border-radius: 5px;
    width: 19rem;
    height: 23rem;
    margin: 2rem 1rem;
}

.products .card img{
    width: 200px;
    height: 200px;
}

.products .card .card-text{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.products .cards h2{
    margin: 15px 0;
    font-size: 20px;
}

.products .cards p{
    font-size: 18px;
    margin: 0;
}

.products .cards button{
    font-size: 15px;
    margin: 13px 0;
    padding: 5px 6px;
    border: 1px solid black;
    border-radius: 2px;
    color: black;
    background-color: white;
}

.products .cards button a{
    text-decoration: none;
    color: black;
}

.products .cards button:hover{
    cursor: pointer;
    background-color: #ebebeb;
    transition: background-color 0.2s ease-in;
}

/* Responsive */
@media screen and (max-width: 1000px) {
    .products .card{
        width: 17rem;
    }

    .product{
        margin: 0;
    }
}

@media screen and (max-width: 900px) {
    .products .card {
        width: 15rem;
    }
    
    .products .card img {
        width: 180px;
        height: 180px;
    }
}

@media screen and (max-width: 580px) {
    .products .card {
        width: 14rem;
        margin: 1rem;
        height: 21rem;
    }

    .products .card img {
        width: 160px;
        height: 160px;
    }
}

@media screen and (max-width: 500px) {
    .product h2 {
        font-size: 40px;
        text-align: center;
    }

    .products .card {
        width: 49%;
        margin: 0;
        margin: 10px 0;
    }

    .products .card img {
        width: 135px;
        height: 135px;
    }

    .products .cards h2 {
        font-size: 18px;
    }

    .products .card .card-text {
        align-items: center;
    }
}

@media screen and (max-width: 400px) {
    .products .card {
        width: 75%;
        height: 19rem;
        margin: 10px 0;
    }

    .products .button{
        margin: 15px 0;
    }

    .product h2 {
        font-size: 35px;
    }

    .button .btn {
        font-size: 14px;
        margin: 6px;
        padding: 8px;
    }
}

@media screen and (max-width: 300px) {
    .product h2 {
        font-size: 32px;
    }
}
.product-info{
    display: flex;
    align-items: center;
    margin: 2rem 0;
    background-color: white;
}

.product-info img{
    margin: 3rem 5rem;
    height: 400px;
    width: 400px;
}

.product-text{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 50%;
    margin-left: 4rem;
}

.product-text .category{
    text-transform: uppercase;
    color: grey;
    font-size: 24px;
    font-weight: 400;
    margin: 0;
}

.product-text .title{
    font-size: 2.5rem;
    font-weight: 400;
    margin: 20px 0;
}

.product-text .rating{
    font-size: 20px;
    font-weight: 700;
    margin: 0;
}

.product-text .price{
    font-size: 25px;
    margin: 20px 0;
    letter-spacing: 1px;
}

.product-text .description{
    margin: 0;
    font-weight: 300;
    font-size: 18px;
}

.product-text .buttons{
    margin: 2rem 0;
}

.product-text .buttons button:hover{
    cursor: pointer;
}

.product-text .add{
    background-color: white;
    border: 1px solid black;
    padding: 8px 10px;
    border-radius: 3px;
    font-size: 15px;
}

.product-text .go{
    color: white;
    background-color: black;
    border: 1px solid black;
    margin: 0px 10px;
    padding: 8px 10px;
    border-radius: 3px;
    font-size: 15px;
}

/* Responsiveness */
@media screen and (max-width: 1000px) {
    .product-info img {
        height: 300px;
        width: 300px;
    }

    .product-text {
        width: 50%;
        margin: 2rem 10px 0 2rem;
    }
}

@media screen and (max-width: 800px) {
    .product-text {
        margin: 2rem 10px 0 0rem;
    }

    .product-text .category {
        font-size: 22px;
    }

    .product-text .title {
        font-size: 2rem;
    }

    .product-text .rating {
        font-size: 18px;
    }

    .product-text .price {
        font-size: 20px;
    }

    .product-text .add, .product-text .go {
        font-size: 13px;
    }

    .product-info img {
        margin: 3rem;
        height: 270px;
    }
}

@media screen and (max-width: 700px) {
    .product-info{
        flex-direction: column;
    }

    .product-info img {
        height: 230px;
        width: 230px;
    }

    .product-text{
        margin: 0;
        align-items: center;
        width: 80%;
    }

    .product-text .price {
        display: block;
        width: auto;
    }

    .product-text .description {
        text-align: center;
    }
}

@media screen and (max-width: 450px) {
    .product-info{
        margin: 0;
    }

    .product-info img {
        height: 200px;
        width: 200px;
        margin: 30px 0;
    }

    .product-text .category {
        font-size: 20px;
    }

    .product-text .title {
        font-size: 28px;
        margin: 15px 0;
    }

    .product-text .price {
        margin: 15px 0;
    }
}

@media screen and (max-width: 400px) {
    .product-text .description {
        font-size: 16px;
    }
}

@media screen and (max-width: 320px) {
    .product-info img {
        height: 170px;
        width: 170px;
    }

    .product-text .category {
        font-size: 18px;
    }

    .product-text .title {
        font-size: 25px;
    }

    .product-text .rating {
        font-size: 17px;
    }

    .product-text .price {
        margin: 15px 0;
    }
}
/* Cart */
.cart{
    display: flex;
    justify-content: center;
    align-items: center;
}

.cart .empty{
    text-align: center;
    margin: 8rem 0;
}

/* Cart Items */
.cart-items{
    width: 50%;
}

/* Product Section*/
.product-section{
    display: flex;
    margin: 2rem 5rem;
    background-color: white;
    justify-content: space-between;
    padding: 0 25px;
}

.cart-product{
    display: flex;
    justify-content: center;
    /* width: 120%; */
}

.cart-product img{
    height: 120px;
    width: 120px;
    margin: 20px;
}

.details .title{
    margin: 10px 0 5px 0;
}

.details .category{
    margin: 10px 0;
}

.details .rating{
    margin: 7px 0;
}

.details .data{
    margin: 11px 0;
}

.details .quantity{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 85px;
}

.details .quantity button{
    margin-bottom: 5px;
    background-color: white;
    border: 1px solid black;
    border-radius: 5px;
    padding: 2px 8px;
}

/* Item Price */
.price{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 200px;
}

.price p{
    margin: 1px 0 5px 0;
}

.quantity p{
    margin: 10px 0;
}

/* Order Summary */
.order{
    background-color: white;
    padding: 20px 30px;
}

.order h3{
    text-align: center;
}

.order .coupon-code input{
    padding: 10px;
    margin: 10px 15px 10px 0;
}

.order .coupon-code button{
    padding: 10px;
}

/* Icons */
.delete-icon{
    margin-top: 12px;
}

.delete-icon:hover{
    cursor: pointer;
}

/* Responsive */
@media screen and (max-width: 1300px) {
    .cart-items {
        width: 65%;
    }
}

@media screen and (max-width: 950px) {
    .cart-items{
        width: 80%;
    }
}

@media screen and (max-width: 750px) {
    .cart-items {
        width: 100%;
    }
}

@media screen and (max-width: 575px) {
    .product-section{
        margin: 2rem 2rem;
    }
}

@media screen and (max-width: 500px) {
    .product-section{
        padding: 0 10px;
    }
    
    .cart-product{
        align-items: center;
    }

    .cart-product img {
        height: 105px;
        width: 100px;
        margin: 12px
    }
}

@media screen and (max-width: 430px) {
    .cart-product img {
        height: 100px;
        width: 95px;
    }

    .details .title {
        font-size: 16px;
        margin: 15px 0 0px 0;
    }

    .details .category {
        font-size: 15px;
        margin: 5px 0;
    }
    
    .data .price{
        width: 100%;
        flex-direction: column;
        align-items: flex-start;
    }

    .details .rating{
        font-size: 14px;
        margin: 0;
    }

    .details .data{
        margin: 5px 0;
    }
    
    .data .price p {
        font-size: 14px;
    }
}

@media screen and (max-width: 300px) {
    .cart-product img {
        height: 74px;
        width: 75px;
    }
}
.about {
    margin: 40px 20px;
    text-align: center;
    font-size: 17px;
}
